:host {
  display: block;
  
  .helper {
    width: 0;
    height: 0;
    overflow: hidden;
    visibility: hidden;
    div {
      width: 9999px;
    }
    span {
      padding: 8px;
    }
  }
  .error-table-outter {
    width: 100%;
    // padding: 15px;
    overflow: auto;
  }

  .table {
    width: 100%;
    max-width: 100%;
    margin-bottom: 20px;
    border-spacing: 0;
    border-collapse: collapse;
  }
  .table > thead > tr > th,
  .table > tbody > tr > th,
  .table > tfoot > tr > th,
  .table > thead > tr > td,
  .table > tbody > tr > td,
  .table > tfoot > tr > td {
    padding: 8px;
    line-height: 1.42857143;
    vertical-align: top;
    border-top: 1px solid #dddddd;
  }
  .table-bordered > thead > tr > th,
  .table-bordered > tbody > tr > th,
  .table-bordered > tfoot > tr > th,
  .table-bordered > thead > tr > td,
  .table-bordered > tbody > tr > td,
  .table-bordered > tfoot > tr > td {
    border: 1px solid #dddddd;
  }
  .table > thead > tr > th {
    vertical-align: bottom;
    border-bottom: 2px solid #dddddd;
  }


  .table-bordered {
    border: 1px solid #ddd;
  }

  .error-table {
    margin-bottom: 0;
    table-layout: fixed;  

    .objectId-td {
      width: 120px;
      max-width: 254px;
      overflow: hidden;
      word-wrap: break-word;
    }

    .message {
      max-width: 560px;
      overflow: auto;
      word-wrap: break-word;
    }
  }

}
